import React from "react"
import { Link } from "gatsby"


import b1 from "../data/image/b2.png"

const Header = ({children} )=>
{
    return(


        <div style={{width:"100%", magin:"auto", }}>
            <div style= {{ backgroundColor:"#615045", borderBottom:"1px solid #4C3A30",color :"white", height:50}}>
                <div style= {{ width:1280, marginTop:80,  color :"white", margin:"auto" ,display: "flex"}}>

                    <h4 style={{marginLeft:"2%",  marginTop:15, fontSize:16 }}> NAVER </h4>
                    <h4 style={{marginLeft:"2%",  marginTop:15, fontSize:16 }}> 네이버쇼핑 </h4>

                    <button style={{ cursor:"loading", height:20, marginTop:15, marginLeft: "78%" , backgroundColor:"#68584E", border:"1px #564A3D" ,color:"white"}}> 로그인 </button>

                    <img src={b1} style={{marginTop:10, width:30, height:30}} alt="headerimg"/>

                </div>
            </div>


            <div style= {{ backgroundColor:"#615045", borderBottom:"1px solid #4C3A30",color :"white", height:80}}>

                <div style= {{ width:1280, marginTop:80,  color :"white", margin:"auto" ,display: "flex"}}>
                    <span style = {{position:"relative", backgroundColor:"#fff", border:"2px #a72525 solid",marginLeft:"2%", marginTop:14, width:50, height:50, WebkitBorderRadius:100, MozBorderRadius:100  } }></span>


                    <span style = {{position:"relative", marginLeft:30, marginTop:30}}>
                        <Link to = "tapmenu_intro" style={{textDecoration:"none", color:"white"}}>
                            고비캐시미어 및 몽골캐시미어
                        </Link>
                    </span>

                    <span style = {{position:"relative", backgroundColor:"#fff", left:700, border:"2px #fff solid", marginTop:25, width:200, height:30, WebkitBorderRadius:100, MozBorderRadius:100  } }/>

                </div>
            </div>


        <div style = {{ width: 1280, backgroundColor:"white", margin:"auto",  height: 80, fontSize: 40 ,textAlign:"center" }}>
            <p style={{ top:80, textAlign:"center"}}> 몽골리아 다판다 </p>
        </div>




        </div>
    )
}

export default Header