const Item2 = ({name, content ,count})=>{
    return(
    <div  style={{ margin:"auto", marginTop:10, cursor: "pointer" ,width:1280, height:199, textAlign:"left"  }} >


        <Link to="detail_story" style={{textDecoration:"none", color:"black"}}>
            <img src ={itemimg} style={{width:279,height:158, float:"left"}}  alt="dsdd" />

            <div style={{  marginLeft:40, width:900, display:"inline-block" }}>

            <p>   스토어찜 10,000명 돌파!  </p>
            <p style={{fontSize:12}}>
                고비캐시미어 전문 스토어샵 중 유일무이 스토어찜 1만명 돌파!!!
                여러분의 많은 사랑 감사합니다~
                네이버에 고비 캐시미어, 몽골캐시미어를 전문적으로 파는 몇몇 스토어팜들이 있습니다만,
                2020년 1월에 저희 몽골리아다판다가 최초로 스토어찜 1만명 스토어팜에 등극하였습니다.
             </p>
            <p>   2020.02.03 조회수 35  </p>
            </div>
        </Link>

         <hr width =  "100%" color = "gray" size = "1" />

    </div>
    )
}


export default ({children}) =>
{
    const data = [0,1,2,3,4,5];
    const dataList = data.map((data, index)=>(
        <Item2 key={index}>
            {data}
        </Item2>
    ));

    return(
        <Layout>
            <div style={{  margin:"auto", width:"100%", textAlign:"center"}}>
                <TapMenu  type={1}/>

                <div style={{ marginTop:40 }}>

                {dataList}



                </div>
            </div>


            <Pagination count={6} link={"/detail_story"}/>
        </Layout>

    )
}


