import React from "react"
import style from "./tapmenu.module.css"
import { Link } from "gatsby";


const TapMenu = ({type})=>
{
    const a = style.test_btn1;
    const b = style.test_btn2;

    let Data=[b,b,b,b];

    if(type === 0)
    {
        Data=[a,b,b,b];
    }
    else if(type === 1)
    {
        Data=[b,a,b,b];
    }
    else if(type === 2)
    {
        Data=[b,b,a,b];
    }
    else if(type === 3)
    {
        Data=[b,b,b,a];
    }

    return(



        <div style={{ width:1280, margin:"auto",textAlign:"left"}}>

            <Link to="/tapmenu_intro">
                <div className={Data[0]} style={{marginLeft:0 }} >
                    소개 페이지
                </div>
            </Link>

            <Link to="/tapmenu_story">
                <div className={Data[1]}>
                    쇼핑스토리
                </div>
            </Link>

            <Link to="/tapmenu_notice">
                <div className={Data[2]}>
                    공지사항
                </div>
            </Link>

            <Link to="/tapmenu_event">
                <div className={Data[3]}>
                    리뷰이벤트
                </div>
            </Link>
        </div>



    )
}



export default TapMenu