import React from "react"
import Style from "./pagination.module.css"
import { Link } from "gatsby"

function testdata(_cnt)
{
    let data =[]
    for(let i = 1; i<_cnt+1;++i)
    {
        data.push(i);
    }
    return data;
}


const Pagination = ({count, link} )=>
{

    const data = testdata(count);
    const dataList = data.map((data,index)=>
    (
        <Link to={link} key ={index}>
            {data}
        </Link>
    ))


    return(

        <div style={{  margin:"auto", width:1280, textAlign:"center"}}>
            <div name="wrap" style={{  margin:"auto", marginTop:100,  textAlign:"center", display:"inline-block"}}>
                <div className = {Style.pagination}>

                    {dataList}

                </div>
            </div>
        </div>
    )
}



export default Pagination